<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.type"
                        label=""
                        disabled
                        label-align="left"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.subject"
                        label=""
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showSubjectPicker=true"
                    />
                    <van-popup v-model="showSubjectPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="subjects"
                            @cancel="showSubjectPicker = false"
                            @confirm="onBSubjectConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.dept"
                        label=""
                        disabled
                        label-align="left"
                    />
                </van-col>
                <van-col span="12">
                    <van-field
                        v-model="baseData.purpose"
                        label=""
                        label-align="left"
                        autosize
                        rows="1"
                        type="textarea"
                    />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker=true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"/>
                    </van-popup> 
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker=true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="shippers"
                            @cancel="showShipperPicker = false"
                            @confirm="onShipperConfirm"/>
                    </van-popup> 
                </van-col>
            </van-row>
        </div>
        
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialNumber"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialBatch"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockName"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockPlace"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.materialSum"
                                label=""
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <!-- <van-col span="12">
                            <van-field
                                required
                                v-model="item.materialActualSum"
                                label=""
                                label-align="left"
                                type="number"
                            />
                        </van-col> -->
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.unitName"
                                label=""
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <!-- <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row> -->
                </van-collapse-item>
            </van-collapse>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="ReqActiveName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in ReqMaterialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialNumber"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.materialBatch"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockName"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.stockPlace"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.materialSum"
                                label=""
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.unitName"
                                label=""
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                required
                                v-model="item.materialActualSum"
                                label=""
                                label-align="left"
                                type="number"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.unitName"
                                label=""
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber:'',
            baseData:{
                type:'',
                typeId:'',
                subject:'',
                subjectId:'',
                purpose:'',
                dept:'',
                deptId:'',
                shipper:'',
                shipperId:'',
                safeKeeping:'',
                safeKeepingId:''
            },
            activeName:'',
            ReqActiveName:'',
            showSubjectPicker:false,
            materialList:[],
            ReqMaterialList:[],
            subjects:[{id:1,text:133},{id:2,text:13}],
            showShipperPicker:false,
            shippers:[{id:1,text:''},{id:2,text:''}],
            showSafekeepingPicker:false,
            safekeepings:[]
        };
    },
    created(){
        this.searchProFeedInfo();
        this.searchAllAccount();
        this.searchAllEmp();
    },
    mounted(){
     window.scannerSearch=this.scannerSearch;
    },
    methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        searchProFeedInfo(){
            this.showLoading('...');
            this.$api.get("/searchProFeedInfo",{feedNumber:this.$route.query.number},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.materialList=response.data.data;
                        this.baseData.type=response.data.typeName;
                        this.baseData.typeId=response.data.type;
                        this.baseData.dept=response.data.dept;
                        this.baseData.deptId=response.data.deptId;
                        Toast.clear();
                    } else{
                        Toast.fail(response.data.msg);
                        Toast.clear();
                        return;
                    }
                } else {
                    Toast.fail("");
                    Toast.clear();
                    return;
                }
            });
        },
        searchAllAccount(){
            this.showLoading('...');
            this.$api.get("/searchAllAccount",{},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.subjects=response.data.subjects;
                        Toast.clear();
                    } else{
                        Toast.fail(response.data.msg);
                        Toast.clear();
                        return;
                    }
                } else {
                    Toast.fail("");
                    Toast.clear();
                    return;
                }
            });
        },
        searchAllEmp(){
            this.$api.get("/selectAllEmpInfo",{},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        this.shippers=response.data.data;
                        this.safekeepings=response.data.data;
                        Toast.success("");
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.fail("");
                    return;
                }
            });
        },
        onSubmit() {
            Toast.loading({
                duration:0,
                message: '...',
                forbidClick: true
            });
            console.log(JSON.stringify(this.baseData))
            this.$api.post("/submitProductRequisit",{baseData:JSON.stringify(this.baseData),materialList:JSON.stringify(this.ReqMaterialList)},response=>{
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("");
                        android.back();
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("");
                    return;
                }
            });
        },
        onBack(){
            this.$router.go(-1);
        },
        onBSubjectConfirm(data){
            this.baseData.subject=data.text;
            this.baseData.subjectId=data.id;
            this.showSubjectPicker=false;
        },
        deleteMaterial(){
            this.ReqMaterialList.some((item, i)=>{
    if(item.id==this.ReqActiveName){
    this.ReqMaterialList.splice(i, 1)
return true
}
})
        },
        onSearch(){
            if(this.barcodeNumber.length==0){
                return;
            }
            this.showLoading('...');
            this.$api.get("/searchBarcodeNumber",{BarcodeNumber:this.barcodeNumber},response=>{
                Toast.clear();
                this.barcodeNumber='';
                this.focusNextInput();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        Toast.success("");
                        var materialInfoList=JSON.parse(response.data.msg);
                        this.materialList.some((item,i)=>{
                            // if(item.stockPlace=="*"){
                            //     item.stockPlace="";
                            // }
                            // materialInfoList.some((materialInfo,i)=>{
                            //     if(item.materialNumber==materialInfo.mNumber&&item.materialBatch==materialInfo.batch&&item.stockName==materialInfo.warehouse&&item.stockPlace==materialInfo.position){
                            //         item.materialActualSum=Number(item.materialActualSum)+Number(materialInfo.mSum);
                            //         this.activeName=item.id;
                            //     }
                            // });
                            if(item.stockPlace=="*"){
                                item.stockPlace="";
                            }
                            materialInfoList.some((materialInfo,i)=>{
                                if(item.materialNumber==materialInfo.mNumber&&item.materialBatch==materialInfo.batch&&item.stockName==materialInfo.warehouse){
                                    var flag=true;
                                    this.ReqMaterialList.some((saleItem,i)=>{
                                        if(saleItem.materialNumber==materialInfo.mNumber&&saleItem.materialBatch==materialInfo.batch&&saleItem.stockName==materialInfo.warehouse&&saleItem.stockPlace==materialInfo.position){
                                            saleItem.materialActualSum=Number(saleItem.materialActualSum)+Number(materialInfo.mSum);
                                            flag=false;
                                            this.ReqActiveName=saleItem.id;
                                        }
                                    });
                                    if(flag){
                                        let saleMate=JSON.parse(JSON.stringify(item));
                                        saleMate.id=this.ReqMaterialList.length+1;
                                        saleMate.materialActualSum=Number(materialInfo.mSum);
                                        saleMate.stockPlace=materialInfo.position;
                                        saleMate.stockPlaceId=materialInfo.positionId;
                                        this.ReqMaterialList.push(saleMate);
                                        this.ReqActiveName=saleMate.id;
                                    }
                                }
                            });
                        });
                    } else {
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("");
                    return;
                }
            });
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.onSearch();
        },
        onShipperConfirm(data){
            this.baseData.shipper=data.text;
            this.baseData.shipperId=data.id;
            this.showShipperPicker=false;
        },
        onSafekeepingConfirm(data){
            this.baseData.safeKeeping=data.text;
            this.baseData.safeKeepingId=data.id;
            this.showSafekeepingPicker=false;
        },
        focusNextInput(){
            var inputs = document.getElementsByTagName("input");
            inputs[0].focus();
        }
    }

}
</script>



