<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fdeptName" label="" label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker = true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="Safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"
                        />
                    </van-popup>
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fempName" label="" label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker = true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker show-toolbar :columns="shippers" @cancel="showShipperPicker = false" @confirm="onShipperConfirm" />
                    </van-popup>
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.inStockName" label="" label-align="left" />
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.inStockPlace" label="" label-align="left" />
                </van-col>
            </van-row>
        </div>

        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.mNumber" :name="item.id" v-for="item in newMaterialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.mNumber" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.batch" label="" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field disabled v-model="item.mSum" label="" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.unitName" label="" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.inStockName" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.inStockPlace" label="" label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.warehouse" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.position" label="" label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber: '',
            baseData: {
                // type:'',
                // typeId:'',
                // salesWay:'',
                // salesWayId:'',
                // purpose:'',
                // dept:'',
                // deptId:'',
            },
            activeName: '',
            showSalesWayPicker: false,
            salesWays: [
                // { id: 1, text: 133 },
                // { id: 2, text: 13 }
            ],
            showShipperPicker: false,
            shippers: [
                // { id: 1, text: '' },
                // { id: 2, text: '' }
            ],
            showSafekeepingPicker: false,
            Safekeepings: [],
            materialList: [],
            newMaterialList: []
        };
    },
    created() {
        this.$api.get('/selectAllEmpInfo', {}, response => {
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if (response.data.success == 'true') {
                    this.shippers = response.data.data;
                    this.Safekeepings = response.data.data;
                    Toast.success('');
                } else {
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail('');
                return;
            }
        });
    },
    methods: {
        showLoading(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        onSubmit() {
            this.showLoading('...');
            this.$api.post(
                '/submitPurchaseReceipt',
                { baseData: JSON.stringify(this.baseData), materialList: JSON.stringify(this.newMaterialList) },
                response => {
                    Toast.clear();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('');
                            android.back();
                        } else {
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.fail('');
                        console.log(response);
                        return;
                    }
                }
            );
        },
        onBack() {
            this.$router.go(-1);
        },
        onSalesWayConfirm(data) {
            this.baseData.salesWay = data.text;
            this.baseData.salesWayId = data.id;
            this.showSalesWayPicker = false;
        },
        onShipperConfirm(data) {
            this.baseData.shipper = data.text;
            this.baseData.shipperId = data.id;
            this.showShipperPicker = false;
        },
        onSafekeepingConfirm(data) {
            this.baseData.safeKeeping = data.text;
            this.baseData.safeKeepingId = data.id;
            this.showSafekeepingPicker = false;
        },
        deleteMaterial() {
            this.newMaterialList.some((item, i) => {
                if (item.id == this.activeName) {
                    this.newMaterialList.splice(i, 1);
                    return true;
                }
            });
        },
        onSearch() {
            if (this.barcodeNumber.length == 0) {
                return;
            }
            var flag = true;
            this.newMaterialList.some((item, i) => {
                if (item.NbarcodeNumber == this.barcodeNumber) {
                    Toast.fail('');
                    flag = false;
                    return;
                }
            });
            // this.submitSum();
            if (flag) {
                this.$api.get('/searchBarcodeNumber', { BarcodeNumber: this.barcodeNumber }, response => {
                    Toast.clear();
                    this.focusNextInput();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('');
                            let materialInfo = JSON.parse(response.data.msg);
                            console.log(materialInfo);
                            materialInfo.some((materialInfoitem, i) => {
                                console.log(1);
                                let saleMate = materialInfoitem;
                                console.log(saleMate);
                                saleMate.id = this.newMaterialList.length + 1;
                                saleMate.inStockPlace = this.baseData.inStockPlace;
                                saleMate.inStockName = this.baseData.inStockName;
                                //saleMate.fstockNumber = this.baseData.stackPlace;
                                this.newMaterialList.push(saleMate);
                                console.log(this.newMaterialList);
                                this.activeName = saleMate.id;
                                this.barcodeNumber = '';
                            });
                        } else {
                            Toast.clear();
                            Toast.fail(response.data.msg);
                            this.barcodeNumber = '';
                            return;
                        }
                    } else {
                        Toast.clear();
                        Toast.fail('');
                        this.barcodeNumber = '';
                        return;
                    }
                });
            }
        },

        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.onSearch();
        },

        focusNextInput() {
            var inputs = document.getElementsByTagName('input');
            inputs[0].focus();
        }
        // submitSum() {
        //     let sumMaterialList = this.newMaterialList;
        //     console.log(1);
        //     console.log(sumMaterialList);
        // }
    }
};
</script>



