<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                ref="search"
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="dealBarcodeNumber"
                style="margin-right:1rem;margin-left:0.1rem"
                right-icon
                @keyup.enter="dealBarcodeNumber"
                >
                <div slot="action" disabled="loading" @click="dealBarcodeNumber"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix='icon' name="saomiao" /></div>
            </van-search>
        </div>
        
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialBarcode" :name="item.id" v-for="item in materialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.mName"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.mNumber"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.mSum"
                                label=""
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.unitName"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.warehouse"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.position"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-field
                                v-model="item.batch"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>

        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="dealActiveName" accordion>
                <van-collapse-item :title="item.materialBarcode" :name="item.id" v-for="item in dealMaterialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.mName"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.mNumber"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                disabled
                                v-model="item.mSum"
                                label=""
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.unitName"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field
                                v-model="item.warehouse"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                        <van-col span="12">
                            <van-field
                                v-model="item.position"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="24">
                            <van-field
                                v-model="item.batch"
                                label=""
                                disabled
                                label-align="left"
                            />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>

        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            baseData:{
                printMachine:'',
                labelId:'4',
                labelName:''
            },
            barcodeNumber:'',
            boxBarcodeNumber:'',
            activeName:'',
            dealActiveName:'',
            materialList:[],
            dealMaterialList:[]
        };
  },
  mounted(){
     window.scannerSearch=this.scannerSearch;
  },
  methods: {
        showLoading(data){
            Toast.loading({
                duration:0,
                message: data,
                forbidClick: true
            });
        },
        dealBarcodeNumber(){
            if(this.barcodeNumber.length==0){
                return;
            }
            if(this.barcodeNumber.length<13&&this.materialList.length==0){
                Toast.fail("");
                return;
            }
            var flag=false;
            this.dealMaterialList.some((item, i)=>{
    if(item.materialBarcode==this.barcodeNumber){
                    flag=true;
    return;
}
});
            if(flag){
                Toast.fail("");
return;
            }
            Toast.loading({
                duration:0,
                message: '...',
                forbidClick: true
            });
            var bar=this.barcodeNumber;
            this.$api.get("/searchBarcodeNumber",{BarcodeNumber:this.barcodeNumber},response=>{
                this.barcodeNumber='';
                this.focusNextInput();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var json=JSON.parse(response.data.msg);
                        Toast.clear();
                        Toast.success("");
                        if(bar.length>13){
                            this.materialList=[];
                            this.dealMaterialList=[];
                            var len=this.materialList.length
                            json.some((item,i)=>{
                                this.$set(item,'id',len+i+1);
                                this.$set(item,'materialBarcode',item.barcodeNumber);
                                this.materialList.push(item);
                            });
                            this.boxBarcodeNumber=bar;
                        }else{
                            var flag=true;
                            var mate='';
                            this.materialList.some((item,i)=>{
                                if(item.barcodeNumber==bar){
                                    flag=false;
                                    mate=item;
                                    return;
                                }
                            });
                            if(flag){
                                Toast.fail("");
                            }else{
                                mate.id=this.dealMaterialList.length+1;
                                this.dealMaterialList.push(mate);
                            }
                        }
                    } else{
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("");
                    return;
                }
            });

            // var id=this.materialList.length+1;
            // var item={id:id,materialBarcode:'BZGJH',materialNumber:'123456',materialName:'',materialBatch:'10000',stockName:'',stockPlace:''};
            // this.materialList.push(item);
        },
        onBack(){
            android.back();
        },
        onSubmit(){
            Toast.loading({
                duration:0,
                message: '...',
                forbidClick: true
            });
            var mateBarcode=[];
            this.dealMaterialList.some((item, i)=>{
                mateBarcode.push(item.materialBarcode);
});
            this.$api.post("/modifiyBoxBarcode",{mateBarcodeList:JSON.stringify(mateBarcode),boxBarcodeNumber:this.boxBarcodeNumber},response=>{
                Toast.clear();
                if (response.status >= 200 && response.status < 300) {
                    if(response.data.success=="true"){
                        var msg=response.data.msg;
                        Toast.success(msg);
                        this.materialList=[];
                        this.dealMaterialList=[];
                    } else{
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail("");
                    return; 
                }
            });
        },
        deleteMaterial(){
            this.dealMaterialList.some((item, i)=>{
    if(item.id==this.dealActiveName){
    this.dealMaterialList.splice(i, 1)
return true
}
})
        },
        openScanner(){
            android.openScanner();
        },
        scannerSearch(data){
            this.barcodeNumber=data;
            this.dealBarcodeNumber();
        },
        focusNextInput(){
            var inputs = document.getElementsByTagName("input");
            inputs[0].focus();
        }
    }
}
</script>


