import Vue from 'vue'
import Router from 'vue-router'
import JobBooking from '../components/JobBooking'
import HelloWorld from "../components/HelloWorld"
import RevisePassword from "../components/RevisePassword"
import Inventory from "../components/inventory"
import ProducionFeeding from "../components/ProFeeding"
import ProducionFeedingForProductRequisit from "../components/producionFeedingForProductRequisit"
import ConsignmentNote from "../components/ConsignmentNote"
import ConsignmentNoteForSaleIssue from "../components/ConsignmentNoteForSaleIssue"
import Boxing from "../components/Boxing"
import UnBoxing from "../components/UnBoxing"
import UnPacking from "../components/UnPacking"
import MiscellaneousIssue from "../components/MiscellaneousIssue"
import MiscellaneousReceipt from "../components/MiscellaneousReceipt"
import AuxCheckAll from "../components/AuxCheckAll"
import AuxCheck from "../components/AuxCheck"
import PurchaseReceipt from "../components/PurchaseReceipt"
import ReceiptNotice from "../components/ReceiptNotice"
import ProductionReceipt from "../components/ProductionReceipt"
import ProductionReport from "../components/ProductionReport"
import Requisition from "../components/Requisition"

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/jobBooking',
      name: 'JobBooking',
      component: JobBooking
    },
    {
      path: '/helloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/revisePassword',
      name: 'RevisePassword',
      component: RevisePassword
    },
    {
      path: '/inventoryQuery',
      name: 'inventory',
      component: Inventory
    },
    {
      path: '/producionFeeding',
      name: 'producionFeeding',
      component: ProducionFeeding
    },
    {
      path: '/producionFeedingForProductRequisit',
      name: 'producionFeedingForProductRequisit',
      component: ProducionFeedingForProductRequisit
    },
    {
      path: '/consignmentNote',
      name: 'ConsignmentNote',
      component: ConsignmentNote
    },
    {
      path: '/consignmentNoteForSaleIssue',
      name: 'ConsignmentNoteForSaleIssue',
      component: ConsignmentNoteForSaleIssue
    },
    {
      path: '/boxing',
      name: 'Boxing',
      component: Boxing
    },{
      path: '/unPacking',
      name: 'UnPacking',
      component: UnPacking
    },{
      path: '/miscellaneousIssue',
      name: 'MiscellaneousIssue',
      component: MiscellaneousIssue
    },{
      path: '/miscellaneousReceipt',
      name: 'MiscellaneousReceipt',
      component: MiscellaneousReceipt
    },{
      path: '/auxCheckAll',
      name: 'AuxCheckAll',
      component: AuxCheckAll
    },{
      path: '/auxCheck',
      name: 'AuxCheck',
      component: AuxCheck
    },{
      path: '/purchaseReceipt',
      name: 'PurchaseReceipt',
      component: PurchaseReceipt
    },{
      path: '/receiptNotice',
      name: 'ReceiptNotice',
      component: ReceiptNotice
    },{
      path: '/productionReceipt',
      name: 'ProductionReceipt',
      component: ProductionReceipt
    },{
      path: '/productionReport',
      name: 'ProductionReport',
      component: ProductionReport
    },
    {
      path: '/unBoxing',
      name: 'UnBoxing',
      component: UnBoxing
    },
    {
      path: '/directAllocation',
      name: 'Requisition',
      component: Requisition
    }
  ]
})



