<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="onSearch"
                right-icon
                @keyup.enter="onSearch"
            >
                <div slot="action" disabled="loading" @click="onSearch"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-row>
                <van-col span="12">
                    <van-field
                        v-model="baseData.fposname"
                        label=""
                        disabled
                        label-align="left"
                        readonly
                        clickable
                        @click="showSalesWayPicker = false"
                    />
                    <van-popup v-model="showSalesWayPicker" position="bottom">
                        <van-picker show-toolbar :columns="salesWays" @cancel="showSalesWayPicker = false" @confirm="onSalesWayConfirm" />
                    </van-popup>
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.fsupplyname" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
            </van-row>

            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fdeptName" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.director" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field v-model="baseData.fempName" label="" disabled label-align="left" label-width="2rem" />
                </van-col>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.shipper"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showShipperPicker = true"
                    />
                    <van-popup v-model="showShipperPicker" position="bottom">
                        <van-picker show-toolbar :columns="shippers" @cancel="showShipperPicker = false" @confirm="onShipperConfirm" />
                    </van-popup>
                </van-col>
            </van-row>
            <van-row>
                <van-col span="12">
                    <van-field
                        required
                        v-model="baseData.safeKeeping"
                        label=""
                        disabled
                        label-align="left"
                        label-width="2rem"
                        @click="showSafekeepingPicker = true"
                    />
                    <van-popup v-model="showSafekeepingPicker" position="bottom">
                        <van-picker
                            show-toolbar
                            :columns="Safekeepings"
                            @cancel="showSafekeepingPicker = false"
                            @confirm="onSafekeepingConfirm"
                        />
                    </van-popup>
                </van-col>
                <van-col span="12">
                    <van-field v-model="baseData.stackPlace" label="" label-align="left" />
                </van-col>
            </van-row>
        </div>

        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeName" accordion>
                <van-collapse-item :title="item.materialName" :name="item.id" v-for="item in newMaterialList" :key="item.id">
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.materialNumber" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.FBatchNo" label="" disabled label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field v-model="item.fstockName" label="" disabled label-align="left" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.fstockNumber" label="" label-align="left" />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field disabled v-model="item.fQty" label="" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-field required v-model="item.materialActualSum" label="" label-align="left" type="number" />
                        </van-col>
                        <van-col span="12">
                            <van-field v-model="item.materialUnit" label="" label-align="left" disabled />
                        </van-col>
                    </van-row>
                    <!-- <van-row>
                        <van-col span="12">
                            <van-field v-model="item.NbarcodeNumber" label="" label-align="left" disabled />
                        </van-col>
                    </van-row> -->
                    <van-row>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6"></van-col>
                        <van-col span="6" align="center">
                            <van-button class="button" size="mini" type="danger" @click="deleteMaterial"></van-button>
                        </van-col>
                    </van-row>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" type="danger" @click="onSubmit"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber: '',
            NbarcodeNumber: '',
            baseData: {
                // type:'',
                // typeId:'',
                // salesWay:'',
                // salesWayId:'',
                // purpose:'',
                // dept:'',
                // deptId:'',
            },
            activeName: '',
            showSalesWayPicker: false,
            salesWays: [
                // { id: 1, text: 133 },
                // { id: 2, text: 13 }
            ],
            showShipperPicker: false,
            shippers: [
                // { id: 1, text: '' },
                // { id: 2, text: '' }
            ],
            showSafekeepingPicker: false,
            Safekeepings: [],
            materialList: [],
            newMaterialList: [],
            barcodeList: []
        };
    },
    created() {
        this.showLoading('...');
        var data = this.$route.query.data;
        //console.log(data);
        this.baseData.fposname = data[0].fposname;
        this.baseData.fSupplyID = data[0].fSupplyID;
        this.baseData.fsupplyname = data[0].fsupplyname;
        this.baseData.fDeptID = data[0].fDeptID;
        this.baseData.fdeptName = data[0].fdeptName;
        this.baseData.fEmpID = data[0].fEmpID;
        this.baseData.fempName = data[0].fempName;
        this.baseData.salesMan = data[0].salesMan;
        this.baseData.salesManId = data[0].salesManId;
        this.baseData.shipper = data[0].shipper;
        this.baseData.shipperId = data[0].shipperId;
        this.baseData.fstockName = data[0].fstockName;
        data.some((item, i) => {
            item.materialList.some((mate, j) => {
                this.materialList.push(mate);
                //console.log(mate);
            });
        });
        this.$api.get('/selectAllEmpInfo', {}, response => {
            Toast.clear();
            if (response.status >= 200 && response.status < 300) {
                if (response.data.success == 'true') {
                    this.shippers = response.data.data;
                    this.Safekeepings = response.data.data;
                    Toast.success('');
                } else {
                    Toast.fail(response.data.msg);
                    return;
                }
            } else {
                Toast.fail('');
                return;
            }
        });
    },
    methods: {
        showLoading(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        onSubmit() {
            this.showLoading('...');
            //console.log(this.NbarcodeNumber);
            this.$api.post(
                '/submitPurchaseReceipt',
                {
                    baseData: JSON.stringify(this.baseData),
                    materialList: JSON.stringify(this.newMaterialList),
                    barcodeList: JSON.stringify(this.barcodeList)
                },
                response => {
                    Toast.clear();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('');
                            android.back();
                        } else {
                            Toast.fail(response.data.msg);
                            return;
                        }
                    } else {
                        Toast.fail('');
                        console.log(response);
                        return;
                    }
                }
            );
        },
        onBack() {
            this.$router.go(-1);
        },
        onSalesWayConfirm(data) {
            this.baseData.salesWay = data.text;
            this.baseData.salesWayId = data.id;
            this.showSalesWayPicker = false;
        },
        onShipperConfirm(data) {
            this.baseData.shipper = data.text;
            this.baseData.shipperId = data.id;
            this.showShipperPicker = false;
        },
        onSafekeepingConfirm(data) {
            this.baseData.safeKeeping = data.text;
            this.baseData.safeKeepingId = data.id;
            this.showSafekeepingPicker = false;
        },
        deleteMaterial() {
            this.newMaterialList.some((item, i) => {
                if (item.id == this.activeName) {
                    this.newMaterialList.splice(i, 1);
                    return true;
                }
            });
        },
        onSearch() {
            if (this.barcodeNumber.length == 0) {
                return;
            }
            var flag = true;
            this.barcodeList.some((item, i) => {
                if (item.barcodeNumber == this.barcodeNumber) {
                    Toast.fail('');
                    flag = false;
                    return;
                }
            });
            // this.submitSum();
            if (flag) {
                this.$api.get('/searchBarcodeNumber', { BarcodeNumber: this.barcodeNumber }, response => {
                    Toast.clear();
                    this.focusNextInput();
                    if (response.status >= 200 && response.status < 300) {
                        if (response.data.success == 'true') {
                            Toast.success('');
                            let materialInfo = JSON.parse(response.data.msg);
                            let listJson = JSON.parse(response.data.msg);
                            console.log(materialInfo);
                            this.NbarcodeNumber = this.barcodeNumber;
                            this.materialList.some((item, i) => {
                                if (item.stockPlace == '*') {
                                    item.stockPlace = '';
                                }
                                materialInfo.some((materialInfoitem, i) => {
                                    //console.log(materialInfoitem);
                                    let json = materialInfoitem;
                                    json.stockPlace = materialInfoitem.position;
                                    json.stockPlaceId = materialInfoitem.positionId;
                                    json.fstockNumber = this.baseData.stackPlace;
                                    this.barcodeList.push(json);
                                    // console.log(111);
                                    // console.log(this.barcodeList);
                                    if (
                                        item.materialNumber == materialInfoitem.mNumber &&
                                        item.FBatchNo == materialInfoitem.batch &&
                                        item.fstockName == materialInfoitem.warehouse &&
                                        item.stockPlace == materialInfoitem.position
                                    ) {
                                        var flag = true;
                                        this.newMaterialList.some((saleItem, i) => {
                                            if (
                                                saleItem.materialNumber == materialInfoitem.mNumber &&
                                                saleItem.materialBatch == materialInfoitem.batch &&
                                                saleItem.fstockName == materialInfoitem.warehouse &&
                                                saleItem.stockPlace == materialInfoitem.position
                                            ) {
                                                saleItem.materialActualSum =
                                                    Number(saleItem.materialActualSum) + Number(materialInfoitem.mSum);
                                                flag = false;
                                                this.activeName = saleItem.id;
                                                saleItem.NbarcodeNumber = this.barcodeNumber;
                                                this.barcodeNumber = '';
                                            }
                                        });
                                        if (flag) {
                                            let saleMate = JSON.parse(JSON.stringify(item));
                                            saleMate.id = this.newMaterialList.length + 1;
                                            saleMate.materialActualSum = Number(materialInfoitem.mSum);
                                            saleMate.stockPlace = materialInfoitem.position;
                                            saleMate.stockPlaceId = materialInfoitem.positionId;
                                            saleMate.NbarcodeNumber = materialInfoitem.barcodeNumber;
                                            saleMate.fstockNumber = this.baseData.stackPlace;
                                            this.newMaterialList.push(saleMate);
                                            this.activeName = saleMate.id;
                                            this.barcodeNumber = '';
                                        }
                                    } else if (
                                        item.materialNumber == materialInfoitem.mNumber &&
                                        item.fstockName == materialInfoitem.warehouse
                                    ) {
                                        var flag = true;
                                        this.newMaterialList.some((saleItem, i) => {
                                            if (
                                                saleItem.materialNumber == materialInfoitem.mNumber &&
                                                saleItem.fstockName == materialInfoitem.warehouse &&
                                                saleItem.stockPlace == materialInfoitem.position
                                            ) {
                                                console.log(1);
                                                console.log(saleItem.stockPlace);
                                                console.log(materialInfoitem.position);
                                                console.log(saleItem.fstockName);
                                                console.log(materialInfoitem.warehouse);
                                                saleItem.materialActualSum =
                                                    Number(saleItem.materialActualSum) + Number(materialInfoitem.mSum);
                                                flag = false;
                                                this.activeName = saleItem.id;
                                                saleItem.NbarcodeNumber = this.barcodeNumber;
                                                this.barcodeNumber = '';
                                            }
                                        });
                                        if (flag) {
                                            console.log(2);
                                            let saleMate = JSON.parse(JSON.stringify(item));
                                            saleMate.id = this.newMaterialList.length + 1;
                                            saleMate.materialActualSum = Number(materialInfoitem.mSum);
                                            saleMate.stockPlace = materialInfoitem.position;
                                            saleMate.stockPlaceId = materialInfoitem.positionId;
                                            saleMate.NbarcodeNumber = materialInfoitem.barcodeNumber;
                                            saleMate.fstockNumber = this.baseData.stackPlace;
                                            this.newMaterialList.push(saleMate);
                                            this.activeName = saleMate.id;
                                            this.barcodeNumber = '';
                                        }
                                    } else if (item.materialNumber == materialInfoitem.mNumber) {
                                        var flag = true;
                                        this.newMaterialList.some((saleItem, i) => {
                                            if (
                                                saleItem.materialNumber == materialInfoitem.mNumber &&
                                                saleItem.materialBatch == materialInfoitem.batch &&
                                                saleItem.fstockName == materialInfoitem.warehouse &&
                                                saleItem.stockPlace == materialInfoitem.position
                                            ) {
                                                saleItem.materialActualSum =
                                                    Number(saleItem.materialActualSum) + Number(materialInfoitem.mSum);
                                                flag = false;
                                                this.activeName = saleItem.id;
                                                saleItem.NbarcodeNumber = this.barcodeNumber;
                                                this.barcodeNumber = '';
                                            }
                                        });
                                        if (flag) {
                                            let saleMate = JSON.parse(JSON.stringify(item));
                                            saleMate.id = this.newMaterialList.length + 1;
                                            saleMate.materialActualSum = Number(materialInfoitem.mSum);
                                            saleMate.stockPlace = materialInfoitem.position;
                                            saleMate.stockPlaceId = materialInfoitem.positionId;
                                            saleMate.NbarcodeNumber = materialInfoitem.barcodeNumber;
                                            saleMate.fstockNumber = this.baseData.stackPlace;
                                            this.newMaterialList.push(saleMate);
                                            this.activeName = saleMate.id;
                                            this.barcodeNumber = '';
                                        }
                                    }
                                });
                            });
                        } else {
                            Toast.clear();
                            Toast.fail(response.data.msg);
                            this.barcodeNumber = '';
                            return;
                        }
                    } else {
                        Toast.clear();
                        Toast.fail('');
                        this.barcodeNumber = '';
                        return;
                    }
                });
            }
        },

        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.onSearch();
        },

        focusNextInput() {
            var inputs = document.getElementsByTagName('input');
            inputs[0].focus();
        }
        // submitSum() {
        //     let sumMaterialList = this.newMaterialList;
        //     console.log(1);
        //     console.log(sumMaterialList);
        // }
    }
};
</script>



