<template>
    <div class="body-bg">
        <div class="form-item">
            <van-search
                v-model="barcodeNumber"
                placeholder=""
                show-action
                shape="round"
                clear
                @search="dealBarcodeNumber"
                style="margin-right:1rem;margin-left:0.1rem"
                right-icon
                @keyup.enter="dealBarcodeNumber"
            >
                <div slot="action" disabled="loading" @click="dealBarcodeNumber"></div>
                <div slot="right-icon" @click="openScanner"><van-icon class="iconfont" class-prefix="icon" name="saomiao" /></div>
            </van-search>
        </div>
        <div class="form-item">
            <h2 class="form-header"></h2>
            <van-collapse v-model="activeDocName" accordion>
                <van-collapse-item :title="doc.materialName" :name="doc.id" v-for="doc in docList" :key="doc.id">
                    <van-collapse v-model="activeMateName" accordion>
                        <van-collapse-item :title="item.stockPlace" :name="item.id" v-for="item in doc.materialList" :key="item.id">
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.materialNumber" label="" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.materialName" label="" disabled label-align="left" />
                                </van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.fQty" label="" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.fBatchNo" label="" disabled label-align="left" />
                                </van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">
                                    <van-field v-model="item.fstockName" label="" disabled label-align="left" />
                                </van-col>
                                <van-col span="12">
                                    <van-field v-model="item.stockPlace" label="" disabled label-align="left" />
                                </van-col>
                            </van-row>
                        </van-collapse-item>
                    </van-collapse>
                </van-collapse-item>
            </van-collapse>
        </div>
        <div>
            <van-row>
                <van-col span="6"></van-col>
                <van-col span="6" align="center">
                    <van-button class="button" round size="small" @click="onBack"></van-button>
                </van-col>
                <van-col span="6"></van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
import { Toast } from 'vant';
export default {
    data() {
        return {
            barcodeNumber: '',
            loading: false,
            activeMateName: '',
            activeDocName: '',
            docList: [],
            materialInfo: []
        };
    },
    methods: {
        toastLog(data) {
            Toast.loading({
                duration: 0,
                message: data,
                forbidClick: true
            });
        },
        dealBarcodeNumber() {
            if (this.barcodeNumber.length == 0) {
                Toast.fail('');
                return;
            }
            this.toastLog('...');
            this.$api.get('/searchBarcodeNumber', { BarcodeNumber: this.barcodeNumber }, response => {
                if (response.status >= 200 && response.status < 300) {
                    Toast.clear();
                    if (response.data.success == 'true') {
                        this.activeDocName = 1;
                        Toast.success('');
                        this.materialInfo = JSON.parse(response.data.msg);
                        this.$api.get('/selectInventory', { materialNumber: this.materialInfo[0].mNumber }, response => {
                            if (response.status >= 200 && response.status < 300) {
                                Toast.clear();
                                if (response.data.success == 'true') {
                                    //Toast.success('');
                                    let json = JSON.parse(response.data.msg);
                                    this.$set(json, 'id', this.docList.length + 1);
                                    var j = 0;
                                    this.docList.some((item, i) => {
                                        j = item.materialList.length + i;
                                    });
                                    json.materialList.some((item, i) => {
                                        j = j + 1;
                                        this.$set(item, 'id', j);
                                    });
                                    this.docList.push(json);
                                    Toast.success('');
                                    console.log(this.docList);
                                } else {
                                    Toast.clear();
                                    Toast.fail(response.data.msg);
                                    return;
                                }
                            }
                        });
                    } else {
                        Toast.clear();
                        Toast.fail(response.data.msg);
                        return;
                    }
                } else {
                    Toast.clear();
                    Toast.fail('');
                    return;
                }
            });
        },
        onBack() {
            android.back();
        },

        deleteMaterial() {
            this.docList.some((item, i) => {
                if (item.id == this.activeDocName) {
                    item.materialList.some((mate, j) => {
                        if (mate.id == this.activeMateName) {
                            if (item.materialList.length == 1) {
                                this.deleteConsignment();
                            } else {
                                item.materialList.splice(j, 1);
                            }
                            return true;
                        }
                    });
                }
            });
        },
        deleteConsignment() {
            this.docList.some((item, i) => {
                if (item.id == this.activeDocName) {
                    this.docList.splice(i, 1);
                    return true;
                }
            });
        },
        openScanner() {
            android.openScanner();
        },
        scannerSearch(data) {
            this.barcodeNumber = data;
            this.dealBarcodeNumber();
        }
    }
};
</script>


